@import '../../scss/sizes.scss';

.Main {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: $navbar-height 120px $footer-height 120px;
	height: 100vh;
	padding-top: $navbar-height;
	position: relative;
	z-index: -1;
}

.Left-Side,
.Right-Side {
	width: 50%;
}

.Left-Side {
	text-align: left;
	padding-right: 120px;
}

.Right-Side {
	display: flex;
	flex-wrap: wrap;
	padding-left: 120px;
	gap: 64px;
	justify-content: space-between;
}
//These styles apply only in the Main section
//That's why they are here, not in the FlashCard Back component
.Right-Side > * {
	width: calc(50% - 32px);
	height: 0;
	//padding bottom replaces height: same as width - padding for bottom and border (32 + 24 + 2)px
	padding-bottom: calc(50% - 58px);
	padding-right: 24px;
	padding-left: 24px;
	padding-top: 24px;
}

.Right-Side i {
	//Regarding icons I'll go with font-sizes set on breakpoint
	font-size: 160/24 * 1rem;
	color: #fff;
	padding-top: calc(50% - 80rem / 24);
}

.Right-Side .Square-Soon {
	// 2rem is just big text fitting the square
	font-size: 2rem;
	color: white;
	font-weight: 900;
	width: 100%;
	// padding centers the text vertically; 1rem is half of the font-size;
	padding-top: calc(50% - 1rem);
	cursor: disabled;
}
